import Cell from './Notebook/Cell'
import { eel } from '../Python/eel'

class Notebook {
  constructor (notebookComponent) {
    this.cells = []
    this.NotebookComponent = notebookComponent

    window.eel.expose(createNewCell.bind(this), 'createNewCell')

    function createNewCell (serverCell) {
      this.createNewCell(serverCell)
    }

    this.loadModernUI()
  }

  async loadModernUI () {
    await eel.load_modern_ui()()
  }

  createNewCell (serverCell) {
    console.log(serverCell)
    console.debug('Creating a new cell')
    this.cells.push(new Cell(this.cells.length + 1))

    if (this.NotebookComponent) {
      this.NotebookComponent.setState({
        cells: this.cells
      })
    }
  }
}

export default Notebook
